import PropTypes from 'prop-types';
import { useEffect, useState, useRef } from 'react';
// @mui
import { Box, Card, Typography, Stack, InputAdornment, IconButton } from '@mui/material';
// components
import { SkeletonProductItem } from '../../../../components/skeleton';
// utils
import { fCurrency } from '../../../../utils/formatNumber';
// components
import Image from '../../../../components/Image';
import InputStyle from '../../../../components/InputStyle';
import Iconify from '../../../../components/Iconify';
// _mock
import { _ecommerceUserProductsList } from '../../../../_mock/_ecommerce';
import { fDateTime } from '../../../../utils/formatTime';



ProductCardGroup.propTypes = {
  products: PropTypes.array,
  pos: PropTypes.string,
  onMatch: PropTypes.func,
  onRemove: PropTypes.func,
};

export default function ProductCardGroup({ products, onMatch, onRemove, pos = 'all' }) {
  return (
    <>
      {products.map((product, index) => (
        <Card
          key={index}
          sx={{
            height: 120, position: 'relative',
            borderRadius: 1, border: '1px solid lightgrey', display: 'flex',
            '&:hover': { cursor: 'pointer' }
          }}
          onClick={() => {
            if (pos === 'all')
              onMatch(product)
          }
          }
        >
          <Box minWidth={120} >
            <Image alt={product.name} src={product.cover} ratio="1/1" sx={{ borderRadius: 1 }} />
          </Box>

          <Stack sx={{ justifyContent: 'center', p: 1 }}>
            <Typography variant="subtitle1" >
              {product.name}
            </Typography>
            <Stack direction="row" spacing={0.5}>
              {product.priceSale && (
                <Typography component="span" variant="subtitle2" sx={{ color: 'text.disabled', textDecoration: 'line-through' }}>
                  {fCurrency(product.priceSale)}
                </Typography>
              )}
              <Typography variant="subtitle2" sx={{ color: 'error.main' }} >{fCurrency(product.price)}</Typography>
            </Stack>
            <Typography variant="caption">sflksdj</Typography>
          </Stack>

          {pos === "matched" &&
            <IconButton
              size="small"
              onClick={() => onRemove(product)}
              sx={{
                top: 6, p: '2px', right: 6, position: 'absolute', color: 'common.white',
                bgcolor: 'grey.50080',
                '&:hover': { backgroundColor: 'grey.700' },
              }}
            >
              <Iconify icon={'eva:close-fill'} />
            </IconButton>
          }

        </Card>
      ))}

    </>
  );
}