import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
// @mui
import { Box, Paper, Grid, Typography } from '@mui/material';
// _mock
import _mock from '../../../../_mock';
// route
import { PATH_APPLICATION } from '../../../../routes/paths';
// components
import { MotionInView, varFade } from '../../../../components/animate';
import {StreamCard} from '../../../../components/card'
// ----------------------------------------------------------------------

const allStream = [...Array(15)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  date: _mock.time(index),
  active: 'pass',
}));


const upComingStream = [...Array(3)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  date: _mock.time(index),
  active: 'upcoming'
}));



// -------------
export default function UserStreamsLast() {
  
  const navigate = useNavigate();

  return (
    <>
      <Paper sx={{ p: 1 }}>
        <MotionInView variants={varFade().inUp}>
          <Typography variant="h4" sx={{ mb: 1 }}>
            Up Coming
          </Typography>
        </MotionInView>
        <Grid container spacing={3} columns={{ lg: 5, md: 3, sm: 2, xs: 1 }}>
          {upComingStream.map((item, index) => (
            <Grid item key={index} xs={1}>
              <StreamCard item={item} onClick={()=>navigate(PATH_APPLICATION.profile.stream,{id:''})}/>
            </Grid>
          ))}
        </Grid>
      </Paper>
      <Box sx={{ p: 1 }}>
        <MotionInView variants={varFade().inUp}>
          <Typography variant="h4" sx={{ mb: 1 }}>
            More Stream
          </Typography>
        </MotionInView>
        <Grid container spacing={2} columns={{ lg: 5, md: 3, sm: 2, xs: 1 }}>

          {allStream.map((item, index) => (
            <Grid item key={index} xs={1}>
              <StreamCard item={item} onClick={()=>navigate(PATH_APPLICATION.profile.stream,{id:''})}/>
            </Grid>
          ))}
        </Grid>
      </Box>

    </>

  );
}

// ----------------------------------------------------------------------
