import PropTypes from 'prop-types';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Stack, Paper, Avatar, Typography } from '@mui/material';

// components
import Image from '../Image';
import TextMaxLine from '../TextMaxLine';
// utils
import { fDateTime, fToNow } from '../../utils/formatTime';
import cssStyles from '../../utils/cssStyles';

const BottomOverWidget = styled('div')(({ theme }) => ({
  ...cssStyles(theme).bgBlur({ blur: 4 }),
  position: 'absolute', bottom: 0, left: 0,right:0,top:'69.5%',
  display: 'flex', alignItems: 'center',
  padding: theme.spacing(0, 1, 0, 2),
  backgroundColor: 'rgba(0,0,0,0.3)',
}));

StreamCard.propTypes = {
  item: PropTypes.object.isRequired,
};

export default function StreamCard({ item, ...other }) {
  const theme = useTheme();
  const { avatar, name, title, cover, date, active } = item;
  const oneMonthAge = new Date();
  oneMonthAge.setMonth(oneMonthAge.getMonth() - 1);

  return (
    <Paper sx={{
      my: 2, borderRadius: 1, bgcolor: 'background.neutral', position: 'relative', overflow: 'hidden',
      border: '1px solid lightgrey',
       width: '100%', paddingTop: '80%',
      '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}`, cursor: 'pointer' }
    }} {...other}>
      <Box
        sx={{
          left: '5%', zIndex: 9, top: "5%", position: 'absolute',
          bgcolor: active === 'upcoming' ? 'error.main' : (active === 'live' ? 'primary.main' : 'rgba(0,0,0,0.8)'),
          color: 'white',
          borderRadius: 1, px: 1, fontSize: 12,
        }} >
        {active === 'live'
          ? "Live"
          : (Date.parse(date) > Date.parse(oneMonthAge)) ? fToNow(date) : fDateTime(date)}
      </Box>
      <Image src={cover} ratio="16/9" sx={{ position: 'absolute', top: 0, left: 0, right: 0 }} />
      <Image src={cover} ratio="5/4" sx={{ position: 'absolute', top: '69.5%', left: 0 }} />
      <BottomOverWidget >
        <Stack direction="row" alignItems="center" spacing={1}>
          <Avatar alt={name} src={avatar}  sx={{ border: '2px solid white' ,width:{xs:60,lg:40},height:{xs:60,lg:40} }} />
          <div>
            <TextMaxLine sx={{ color: '#fff ', lineHeight: 1 }} variant={"body2"} line={2}>{title}</TextMaxLine>
            <Typography sx={{ color: '#fff', display: 'block', px: 1 }} variant={"caption"}>
              {name}
            </Typography>
          </div>

        </Stack>

      </BottomOverWidget>

    </Paper >
  );
}