import PropTypes from 'prop-types';
import { useEffect, useState, useRef } from 'react';
// @mui
import { Box, Card, Typography, Stack, InputAdornment } from '@mui/material';
// components
import { SkeletonProductItem } from '../../../../components/skeleton';
// utils
// components
import InputStyle from '../../../../components/InputStyle';
import Iconify from '../../../../components/Iconify';
// _mock
import { _ecommerceUserProductsList } from '../../../../_mock/_ecommerce';

import ProductCardGroup from './ProductCardGroup';
// ----------------------------------------------------------------------

UserProductList.propTypes = {
  currentUser: PropTypes.object // .isRequired,

};

const allProduct = _ecommerceUserProductsList;


export default function UserProductList({ currentUser }) {


  const [match, setMatch] = useState([]);
  const [filterName, setFilterName] = useState('');
  const [filteredProducts, setfilteredProducts] = useState();
  const [allProducts, setAllProducts] = useState(allProduct);

  useEffect(() => {
    onFilterName('')
  }, [currentUser])


  const addMatch = (product) => {

    const temp1 = match.slice();
    const check1 = temp1.filter((matchedProduct) => product.id === matchedProduct.id);
    if (!check1.length) {
      temp1.push(product);
      setMatch(temp1);

      const temp2 = allProducts.slice();
      const check2 = temp2.filter((matchedProduct) => product.id !== matchedProduct.id);
      setAllProducts(check2);

      const tempFilter = filteredProducts.slice();
      const check3 = tempFilter.filter((matchedProduct) => product.id !== matchedProduct.id);
      setfilteredProducts(check3);
    }
  }
  const removeMatch = (product) => {

    const temp1 = match.slice();
    const check1 = temp1.filter((matchedProduct) => product.id !== matchedProduct.id);
    setMatch(check1);

    const temp2 = allProducts.slice();
    temp2.push(product);
    setAllProducts(temp2);

    const temp3 = filteredProducts.slice();
    temp3.push(product);
    setfilteredProducts(temp3);
  }

  const onFilterName = (filterName) => {
    setFilterName(filterName);
    const products = applySortFilter(allProducts, filterName);
    setfilteredProducts(products)
  };



  return (
    <>

      <Card sx={{ p: 2, mt: 3, minHeight: 150 }}>
        <Typography variant="h5" noWrap>
          Matched Product to this Stream
        </Typography>


        {match &&
          <Box
            sx={{
              display: 'grid', gap: 2,
              gridTemplateColumns: {
                xs: 'repeat(1, 1fr)',
                sm: 'repeat(2, 1fr)',
                md: 'repeat(3, 1fr)',
                lg: 'repeat(4, 1fr)',
              },
            }}>
            <ProductCardGroup
              products={match}
              onRemove={(product) => removeMatch(product)}
              pos={'matched'}
            />
          </Box>

        }

      </Card>
      <Card sx={{ p: 2, mt: 3, minHeight: 150 }} >
        <Typography variant="h5" noWrap>
          All Your Products
        </Typography>

        <InputStyle
          stretchStart={240}
          value={filterName}
          onChange={(event) => onFilterName(event.target.value)}
          placeholder="Search product..."
          sx={{ mt: 3 }}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Iconify icon={'eva:search-fill'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
              </InputAdornment>
            ),
          }}
        />
        {filteredProducts &&
          <Box sx={{
            display: 'grid', gap: 2, py: 2, mt: 2,
            gridTemplateColumns: {
              xs: 'repeat(1, 1fr)',
              sm: 'repeat(2, 1fr)',
              md: 'repeat(3, 1fr)',
              lg: 'repeat(4, 1fr)',
            },
          }}>
            <ProductCardGroup products={filteredProducts}
              onMatch={(product) => addMatch(product)} />
          </Box>
        }

      </Card>
    </>
  );
}




function applySortFilter(array, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  if (query) {
    return array.filter((_product) =>
      (_product.name.toLowerCase().indexOf(query.toLowerCase()) !== -1));
  }
  return stabilizedThis.map((el) => el[0]);
}


