
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import { useCallback, useEffect, useMemo } from 'react';

// form
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { styled } from '@mui/material/styles';
import { Box, Typography, Grid, Card, Stack, } from '@mui/material';

// hooks
import useAuth from '../../../../hooks/useAuth';
// 
// import {useDispatch,useSelector,store} from '../../../../redux/store';
// components
import { FormProvider, RHFUploadStoreCoverFile, RHFSelect, RHFSwitch, RHFTextField, RHFUploadAvatar } from '../../../../components/hook-form';
// route 
import { PATH_APPLICATION } from '../../../../routes/paths';

// _mock
import { countries } from '../../../../_mock';
// ----------------------------------------------------------------------




const InfoStyle = styled('div')(({ theme }) => ({

  left: theme.spacing(1),
  bottom: theme.spacing(1),
  zIndex: 99,
  position: 'absolute',
  display: 'flex',
  alignItems: 'center',
  [theme.breakpoints.up('md')]: {
    left: theme.spacing(5),
    bottom: theme.spacing(5),
  },

}));
// ----------------------------------------------------------------------

const isEdit = true;

UserStoreCreate.propTypes = {
  currentUser: PropTypes.object,
};

export default function UserStoreCreate({ currentUser }) {
  const { user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  // const {account, isLoading} = useSelector((state)=>state.account);
  const isLoading = false
  // const { position, cover } = currentUser;


  const NewUserSchema = Yup.object().shape({
    storeName: Yup.string().required('Store is required'),
    storePhoneNumber: Yup.string().required('Phone number is required'),
    storeEmail: Yup.string().required('Email is required').email(),
    storeAddress: Yup.string().required('Address is required'),
    storeCountry: Yup.string().required('country is required'),
    storeState: Yup.string().required('State is required'),
    storeCity: Yup.string().required('City is required'),
    storeAvatarUrl: Yup.mixed().test('required', 'Avatar is required', (value) => value !== ''),
    storeCoverUrl: Yup.mixed().test('required', 'Cover is required', (value) => value !== ''),
  });

  const defaultValues = useMemo(
    () => ({
      // fullName: currentUser?.name || '',
      storeName: currentUser?.storeName || 'Cergey Stroe',
      storeEmail: currentUser?.storeEmail || currentUser?.email || '',
      storeTelNumber: currentUser?.storeTelNumber || '12934484',
      storePhoneNumber: currentUser?.storePhoneNumber || currentUser?.phoneNumber || '12311233',
      storeAbout: currentUser?.storeAbout || 'exampledfasdfasd',


      storeAddress: currentUser?.storeAddress || currentUser?.address || 'Russia Fernand',
      storeCountry: currentUser?.storeCountry || currentUser?.country || 'example',
      storeState: currentUser?.storeState || currentUser?.state || 'example',
      storeCity: currentUser?.storeCity || currentUser?.city || 'example',
      storeZipCode: currentUser?.storeZipCode || '23939',

      storeAvatarUrl: currentUser?.storeAvatarUrl || currentUser?.avatar || '',
      storeCoverUrl: currentUser?.storeCoverUrl || currentUser?.covers || '',
      storeCompany: currentUser?.storeCompany || 'example',

      isStoreVerified: currentUser?.isStoreVerified || true,
    }),
    [currentUser]
  );

  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (currentUser) {
      reset(defaultValues);
    }

  }, [currentUser]);

  const onSubmit = async () => {
    try {
      console.log('submited');
      await new Promise((resolve) => setTimeout(resolve, 500));
      reset();
      enqueueSnackbar(!isEdit ? 'Create success!' : 'Update success!');
    } catch (error) {
      console.error(error);
    }
  };

  const handleAvatarDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          'storeAvatarUrl',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );
  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          'storeCoverUrl',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );


  return (

    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>

      <Box sx={{position: 'relative', width: '100%', paddingTop: 'calc(1/4*100%)', 
      overflow: 'hidden', mb: 5 }}>

        <InfoStyle>
          <RHFUploadAvatar
            name="storeAvatarUrl"
            accept="image/*"
            maxSize={3145728}
            onDrop={handleAvatarDrop}
            sx={{
              mx: 'auto',
              borderWidth: 3,
              borderStyle: 'solid',
              p: 0,
              borderColor: 'primary.main',
              width: { xs: 60, sm: 110, md: 150 },
              height: { xs: 60, sm: 110, md: 150 },
            }}
          />

          {values.storeCoverUrl &&
            <Box sx={{
              ml: -5, pl: 6, py: { md: 1 }, pr: 2,
              color: 'common.white',
              zIndex: -1,
              boxShadow: '0 0 10px 5px rgba(0,0,0,0.58)',
              bgcolor: 'rgba(0,0,0,0.5)',
              borderRadius: 50
            }}
            >
              <Typography variant="h5">{values.storeName}</Typography>
              <Typography sx={{ opacity: 0.72 }}>{values.storeAddress}</Typography>
            </Box>
          }
        </InfoStyle>

        <RHFUploadStoreCoverFile
          name="storeCoverUrl" accept="image/*" maxSize={3145728} onDrop={handleCoverDrop}
          sx={{ borderRadius: 0, position: 'absolute', right: 0, left: 0, top: 0,bottom:'calc(3/4*100%)' }} />
      </Box>

      <Card sx={{ mb: 5, p:3, textAlign: 'center' }}>
        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <RHFTextField name="storeName" label="Store Name" sx={{ mb: 2 }} />
            <RHFTextField name="storeAbout" multiline rows={4} label="About Store" sx={{ mb: 2.5 }} />
            <RHFTextField name="storeZipCode" label="Store Zip/Code" />
          </Grid> 

          <Grid item xs={12} md={8} >
            <Box
              sx={{
                display: 'grid',
                rowGap: 2,
                columnGap: 1,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="storeAddress" label="Contact Address" />
              <RHFTextField name="storeEmail" label="Contact Email Address" />
              <RHFTextField name="storePhoneNumber" label="Contact Phone Number" />
              <RHFSelect name="storeCountry" label="Country" placeholder="storeCountry">
                <option value="" />
                {countries.map((option) => (
                  <option key={option.code} value={option.label}>
                    {option.label}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="storeState" label="Contact State/Region" />
              <RHFTextField name="storeCity" label="Contact City" />
              <RHFTextField name="storeTelNumber" label="Contact Tel Number" />
              <RHFTextField name="storeCompany" label="Parrant Company or Store" sx={{ mb: 2 }} />
            </Box>
          </Grid>
        </Grid>
        <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
          <LoadingButton type="submit" variant="contained" loading={isLoading}>
            Update Store
          </LoadingButton>
        </Stack>
      </Card>

    </FormProvider >

  );
}



