
import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { LoadingButton, MobileDateTimePicker, LocalizationProvider } from '@mui/lab';
import { Box, Card, Grid, Stack, TextField, Switch, Paper, Typography, FormControlLabel } from '@mui/material';
import { useTheme, styled } from '@mui/material/styles';
// utils
import { fDateTime } from '../../../../utils/formatTime';
import cssStyles from '../../../../utils/cssStyles';
// 
import {useDispatch,useSelector,store} from '../../../../redux/store';
// hook
import useResponsive from '../../../../hooks/useResponsive';
// routes
import { PATH_APPLICATION } from '../../../../routes/paths';
// components
import { FormProvider, RHFSelect, RHFSwitch, RHFUploadStoreCoverFile, RHFTextField, RHFUploadAvatar }
  from '../../../../components/hook-form';
import TextMaxLine from '../../../../components/TextMaxLine';

// ----------------------------------------------------------------------

const BottomOverWidget = styled('div')(({ theme }) => ({
  ...cssStyles(theme).bgBlur({ blur: 10 }),
  position: 'absolute', bottom: 0,top:'69.5%', left: 0, right: 0, zIndex: 99,
  display: 'flex', alignItems: 'center',
  padding: theme.spacing(1, 2),
  backgroundColor: 'rgba(0,0,0,0.3)',
  borderBottomLeftRadius:theme.spacing(1),
  borderBottomRightRadius:theme.spacing(1),
  // height: '25%',
  margin: 1,
}));

UserStreamCreate.propTypes = {
  currentUser: PropTypes.object,
}

export default function UserStreamCreate({ currentUser }) {
  const isEdit = false;
  const theme = useTheme();
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const {account, isLoading} = useSelector((state)=>state.account);

  const isDesktop = useResponsive('lg','up')
  const NewUserSchema = Yup.object().shape({
    fullName: Yup.string().required('fullName is required'),
    channelTitle: Yup.string().required('Title is required'),
    channelCoverUrl: Yup.mixed().test('required', 'Cover is required', (value) => value !== ''),
    channelAvatarUrl: Yup.mixed().test('required', 'Avatar is required', (value) => value !== ''),
    channelDescription: Yup.string().required('Description is required'),
    startTime: Yup.date().required('Start Time is required'),
  });

  const defaultValues = useMemo(
    () => ({
      fullName: currentUser?.fullName || '',
      channelTitle: currentUser?.channelTitle || '',
      channelDescription: currentUser?.channelDescription || '',
      channelCoverUrl: currentUser?.channelCoverUrl || '',
      channelAvatarUrl: currentUser?.channelAvatarUrl || currentUser?.avatarUrl || '',
      startTime: currentUser?.startTime || new Date(),
      nowStart: false,
    }),
    [currentUser]
  );

  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();



  useEffect(() => {
    if (isEdit && currentUser) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);

  const onSubmit = async () => {
    try {
      console.log(values);
      await new Promise((resolve) => setTimeout(resolve, 500));
      reset();
      enqueueSnackbar(!isEdit ? 'Create success!' : 'Update success!');
      navigate(PATH_APPLICATION.profile.streams);
    } catch (error) {
      console.error(error);
    }
  };

  const handleAvatarDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setValue(
          'channelAvatarUrl',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );
  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setValue(
          'channelCoverUrl',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Card sx={{ p: 3 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <Paper sx={{
              borderRadius: 1, position: 'relative', overflow: 'hidden',
              width:'100%',paddingTop:'80%',
              '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}` }
            }}>
              <Box
                sx={{
                  left: '5%', zIndex: 9, top: '5%', position: 'absolute',
                  bordercolor: 'error.main',
                  bgcolor: 'error.lighter',
                  border: '1px solid',
                  color: 'error.main',
                  borderRadius: 2, px: 1, fontSize: 16
                }} >
                {fDateTime(values.startTime)}
              </Box>

                <RHFUploadStoreCoverFile
                  name="channelCoverUrl" accept="image/*" maxSize={3145728} onDrop={handleCoverDrop}
                  sx={{ position: 'absolute', top: 0, right: 0, left: 0, bottom: 0 }}
                />
              <BottomOverWidget >
                <Stack direction="row" alignItems="center" spacing={1}>
                  <RHFUploadAvatar
                    name="channelAvatarUrl"
                    accept="image/*"
                    maxSize={3145728}
                    onDrop={handleAvatarDrop}
                    sx={{ width:{xs:70, sm:55,md:85}, height: {xs:70,sm:55,md:85}, border:'3px solid white' ,p:0 }}
                  />
                  <div>
                    <TextMaxLine sx={{ color: '#fff !important' }} variant={"h5"} line={2} >
                      {values.channelTitle || 'Your Stream Title'}
                    </TextMaxLine>
                    <Typography sx={{ color: '#fff', display: 'block', px: 1 }} variant={"body1"} >
                      {values.fullName || 'Your fullname'}
                    </Typography>
                  </div>
                </Stack>
              </BottomOverWidget>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Stack spacing={1.5}>
              <RHFTextField name="fullName" label="Full Name" />
              <RHFTextField name="channelTitle" label="Channel Title" />
              <RHFTextField name="channelDescription" label="Description" multiline rows={4} />

              <RHFSwitch name="nowStart" label="Start Now Streaming" />
              <Controller
                name="startTime"
                control={control}
                render={({ field }) => (
                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <MobileDateTimePicker
                      {...field}
                      minDate={new Date()}
                      inputFormat="dd MMM yyyy hh:mm a"
                      label="Hosting Start Time"
                      renderInput={(params) => <TextField  {...params} fullWidth />}
                    />
                  </LocalizationProvider>
                )}
              />
            </Stack>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton type="submit" variant="contained" size="large" loading={isSubmitting}>
                {!isEdit ? 'Create Channel' : 'Save Changes'}
              </LoadingButton>
            </Stack>

          </Grid>
        </Grid>
      </Card>
    </FormProvider >
  );
}
